.ama-i18n-editor {
  padding: 40px 0px;
}

// All buttons override
.btn {
  min-width: 50px;
  min-height: 50px;

  &.array-item-add {
    max-width: 50px;
    max-height: 50px;
  }
}

.rjsf {
  > .field-object {
    > .form-group {
      > .container-fluid {
        padding-right: calc(var(--bs-gutter-x) * 0.5) !important;
        padding-left: calc(var(--bs-gutter-x) * 0.5) !important;

        > .row {
          border: 1px solid black;
          padding: 15px 0px;
        }
      }
    }
  }
}

// Type Array
.field-array {
  //  Text input in field type array
  input.form-control {
    min-height: 50px;
  }

  .container-fluid {
    //  Align button group with order and delete options, with the input.
    .row {
      &.align-items-center {
        align-items: flex-end !important;
        .py-4 {
          padding: 0 !important;
        }
      }
    }

    //  Align right the add item option
    .container {
      max-width: unset;
      width: 100%;
      .col-9 {
        display: none;
      }
      .col-3 {
        width: 100%;
        display: flex;
        justify-content: flex-end;
      }
    }
  }
}
